import React, { Component } from 'react';
import { Row, Col, Form, Input, Radio } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { DatePickerString } from 'components/common';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'FormSearchCandidate' })
class FormSearchCandidate extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  formItemLayout = 6;

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={this.formItemLayout}>
            <Form.Item label="S CV n">{getFieldDecorator('docInNo')(<Input />)}</Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="n ngy">
              {getFieldDecorator('docInDate')(<DatePickerString />)}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="Ni gi">{getFieldDecorator('sentPlace')(<Input />)}</Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="S vo s">{getFieldDecorator('id')(<Input />)}</Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="Ngy vo">
              {getFieldDecorator('noteDate')(<DatePickerString />)}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="Ni dung">{getFieldDecorator('docContent')(<Input />)}</Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="Ni lu">{getFieldDecorator('savedPlace')(<Input />)}</Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="V k nhn">
              {getFieldDecorator('recipientUnit')(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col
            span={24}
            style={{ display: 'flex', flexDirection: 'row-reverse', alignItems: 'center' }}
          >
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
            <Form.Item style={{ marginBottom: 0 }}>
              {getFieldDecorator('isCompleted', {
                initialValue: null,
              })(
                <Radio.Group>
                  <Radio value={null}>Tt c</Radio>
                  <Radio value="true"> x l</Radio>
                  <Radio value="false">Cha x l</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchCandidate;



