import React, { Component, Fragment } from 'react';
import { withRouter, Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, Btn } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message, Checkbox } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { getFile } from 'services/fileStorage';
import routeConstant from 'routes/route/routeConstant';
import { getDocIns, deleteDocIn, downloadFile } from 'services/docIn';
import { dateToString } from 'utils/formatUtil';
import FormSearchDocIn from './FormSearchDocIn';

class ListDocInPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteDocIn(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  exportDocOut = () => {
    const { paramSearch } = this.state;
    downloadFile(paramSearch);
  };

  exportUser = record => {
    getFile(record.attachmentId);
  };

  render() {
    const columns = [
      {
        title: 'S CV n',
        dataIndex: 'docInNo',
        key: 'docInNo',
      },
      {
        title: 'Ngy n',
        dataIndex: 'docInDate',
        key: 'docInDate',
        render: text => dateToString(text),
      },
      {
        title: 'Ni gi',
        dataIndex: 'sentPlace',
        key: 'sentPlace',
      },
      {
        title: 'S CV vo s',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Ngy vo s',
        dataIndex: 'noteDate',
        key: 'noteDate',
        render: text => dateToString(text),
      },
      {
        title: 'Ni dung',
        dataIndex: 'docContent',
        key: 'docContent',
      },
      {
        title: 'Ni lu',
        dataIndex: 'savedPlace',
        key: 'savedPlace',
      },
      {
        title: ' gi email',
        dataIndex: 'isSentEmail',
        key: 'isSentEmail',
        render: (text, record) => <Checkbox checked={record.isSentEmail} />,
      },
      {
        title: 'V k nhn',
        dataIndex: 'recipientUnit',
        key: 'recipientUnit',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <ColEditDelete
              linkEdit={{
                to: {
                  pathname: routeConstant.TO_CHUC__CONG_VAN__CONG_VAN_DEN__NHAP_DU_LIEU,
                  search: `?id=${record.id}`,
                },
              }}
              onConfirmDelete={() => this.handleDelete(record.id)}
            />
            {record.attachmentId && (
              <Btn style={{ width: 25 }} onClick={() => this.exportUser(record)} icon="download" />
            )}
          </Fragment>
        ),
      },
    ];
    return (
      <Card title="">
        <Row gutter={16}>
          <Col span={24}>
            <FormSearchDocIn />
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={24}>
            <div className="btn-group" style={{ justifyContent: 'flex-start' }}>
              <Link to={routeConstant.TO_CHUC__CONG_VAN__CONG_VAN_DEN__NHAP_DU_LIEU}>
                <BtnAddNew />
              </Link>
              <Btn onClick={this.exportDocOut} icon="download">
                Download
              </Btn>
            </div>
          </Col>
        </Row>
        <Row gutter={16}>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getDocIns}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListDocInPage {...props} />
  </Page>
));



